@media only screen and (min-width: 0px) and (max-width: 950px) {
  .contactContainer{
    display: flex;
    justify-content: space-evenly;
    width:80%;
    margin: auto;
    padding-bottom: 3%;
    flex-direction: column;
    align-items: center;
  }
  .con2{
    height: auto;
    width:auto;
    width:50%;
   
  }
  .conForm>input{
    margin-bottom: 4%;
    padding:2% 2%
  }
  .con1{
    text-align: left;
    
    font-size: 20px;
  }
  
}
