  @media only screen and (min-width: 0px) and (max-width: 1090px) {
 
    .skillContainer{
      display:flex;
      flex-direction: column;
      justify-content: space-around;
      /* border:1px solid red; */
      align-items: center;
    }

    .techSkills,.toolSkills{
      width:60%;
      /* border:1px solid; */
