    }
    .tech,.tools{
      display: grid;
      grid-template-columns:1fr 1fr;
      gap:30px;
      margin-top: 10%;
      
      
    }
    .skillContainer img{
      /* border:1px solid ; */
      background-color: #EDF2F8;
      border-radius: 5px;
