.container {
    margin-top: 0px;
    padding: 20px 5%;
    height: max-content;
    /* border: 1px solid ; */
  }
  
  .heading {
    color: #4070f4;
    text-align: center;
  }
  
  .projects{
    width:80%;
    display:grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows:auto auto;
    margin-top: 2%;
    justify-content: space-around;
    align-items:center;
    /* border: 1px solid red; */
    gap: 80px;
    margin: auto;
  }

  .projects>div,.project>div{
    /* width:100%; */
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 3%;
    /* border:1px solid; */
   
  }
  .bounce {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
  }
  .bounce:hover {
    transform: scale(1.1);
}



  .projects>div:hover{
    /* box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; */
  }

  .projectImg{
    width:100%;
    height:auto;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;  }
  .projects a,.project a{
    text-decoration: none;
    color:black
  }

  .projectButton{
    width:60%;
    margin: auto;
    display: flex;
    justify-content: space-around;
  }
  .projectButton>button{
    background-color: #4070f4;
    color:white;
    padding:3% 5%;
    border:none;
    border-radius:3px;
    margin-bottom:4%;
    cursor: pointer;
  }
  .emptyDiv{
    padding: 0%;
    box-shadow: none;
    display: none;
  }
  .project{
    width:100%;
    /* display:flex; */
    /* margin-top: 2%;
    justify-content: space-around; */
    align-items:center;
    border: 1px solid red;
  }
.projectHeading{
  font-family: Montserrat, "Helvetica Neue", Arial, sans-serif;
  text-align: start;
  color:#859901;
}

.projectDesc{
  font-family:Montserrat, "Helvetica Neue", Arial, sans-serif ;
  text-align: start;
  font-size: 17px;
  line-height: 23px;
  font-weight: 400;
}

.projectTech{
  font-family:Montserrat, "Helvetica Neue", Arial, sans-serif ;
  text-align: start;
  font-size: 20px;
  line-height: 31px;
  font-weight: 400;
  margin-top: 0px;
  margin-bottom: 0px;
  color:#B57615;


}
.projectTech+h5{
  margin-top: 1%;
  font-size: 14px;
  margin-left: 1%;
  font-weight: bold;

}

  @media only screen and (min-width: 0px) and (max-width: 1090px) {
    .projects{
      width:100%;
      display:flex;
      flex-direction: column;
      justify-content: space-around;
      align-items:center
    }
    
    .projects>div{
      width:60%;
      height: auto;
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
     margin-bottom: 5%;
    }
  }
  h3+h4{
    margin: 1%;
  }

  @media only screen and (min-width: 0px) and (max-width: 961px) {
  
    .projects>div{
      width:90%;
      height: auto;
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
     margin-bottom: 5%;
    }
  }